@import url(//spoqa.github.io/spoqa-han-sans/css/SpoqaHanSansNeo.css);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@400;500;700;900&Roboto:wght@500;700;900&display=swap);
body{font-family:'Spoqa Han Sans Neo', 'Roboto', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;overflow-x:hidden}html,body,div{box-sizing:content-box;margin:0;padding:0}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.background-content{width:100%;height:1300px;margin:0;padding:0}.container-content{padding:2rem}.container-content h2{font-size:4rem;text-align:center;margin-top:1rem;margin-bottom:2rem}.container-content p{font-size:1.5rem}#content1{background-color:#e54b4b;color:white}#content2{background-color:#b7e3e4;color:#F03F35}#content3{background-color:#ffefe5;color:#1FC8A9}

